import {
  CircleMode,
  DragCircleMode,
  DirectMode,
  SimpleSelectMode
} from "mapbox-gl-draw-circle";
import MapboxDraw from "@mapbox/mapbox-gl-draw";
import * as mapboxgl from "mapbox-gl";
import { generateFeatures } from "./utils/feature";

const locations = [
  { lon: -96.58018086562365, lat: 44.76489866786821, radiusInKm: 100 },
  { lon: -93.6248586, lat: 41.58527859, radiusInKm: 50 }
];

const initialFeatures = generateFeatures(locations);

mapboxgl.accessToken =
  "pk.eyJ1Ijoic2hmaXNoYnVybiIsImEiOiJjaWs2dHB0cXQwMHdqaHJtMjJ6ejVra2R0In0.Gfuf4QMy6U0MfG1fDddZvQ";
var map = new mapboxgl.Map({
  container: "map",
  style: "mapbox://styles/mapbox/streets-v11",
  center: [-93.6248586, 41.58527859],
  zoom: 5
});

const draw = new MapboxDraw({
  userProperties: true,
  modes: {
    ...MapboxDraw.modes,
    draw_circle: CircleMode,
    drag_circle: DragCircleMode,
    direct_select: DirectMode,
    simple_select: SimpleSelectMode
  }
});

map.on("load", function() {
  map.addControl(draw);
  initialFeatures.map(draw.add);
  map.on("draw.add", e => console.log(e.features));
  map.on("draw.update", e => console.log(e.features));
  map.on("draw.delete", e => console.log(e.features));
});

class MyCustomControl {
  onAdd(map) {
    this.map = map;
    this.container = document.createElement("button");
    this.container.onclick = () => {
      const zoom = this.map.getZoom();
      draw.changeMode("draw_circle", {
        initialRadiusInKm: 1 / Math.pow(2, zoom - 11)
      });
      draw.delete("-96.5801808656236544.76489866786821");
      draw.add(generateFeatures([{ ...locations[0], lat: 41.58527859 }])[0]);
    };
    this.container.className =
      "my-custom-control mapboxgl-ctrl my-custom-control";
    this.container.textContent = "Circle";
    return this.container;
  }
  onRemove() {
    this.container.parentNode.removeChild(this.container);
    this.map = undefined;
  }
}

const myCustomControl = new MyCustomControl();

map.addControl(myCustomControl);
